/* eslint-disable no-unused-vars */
import { Col, Row } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { currencyFormat } from '../../utils/helper/utilities';

export const PrintPage = React.forwardRef((props, ref) => {
	const {
		transactions = [],
		cash = {},
		billDate = '',
		billNo = null,
		paymentMethod = '',
		isReprintBill = false,
		payments = [],
		isPrintTest = false
	} = props;

	const [property, setProperty] = useState({});
	const { billNo: billNoTrx } = useSelector((state) => state?.transaction);
	const { loading = false } = useSelector((state) => state?.auth);

	useEffect(() => {
		const app = JSON.parse(localStorage.getItem('app') || '{}');
		const { property = {}, user = {} } = app;

		setProperty({ ...property, cashier: user?.username || '' } || {});
	}, [ref]);

	const total = transactions?.reduce((acc, { total = 0 }) => +acc + +total, 0);
	const cashPaid = payments?.reduce((acc, { paidValue = 0, cashPay = 0, paidType }) => +acc + +(+paidType === 1 ? cashPay : paidValue), 0);
	const cashChange = cashPaid - total;

	return (
		<div className="w-full h-full rounded-md mx-auto">
			<div className="w-full h-auto bg-white p-3 py-7 mx-auto rounded-md" style={{ maxWidth: '305px' }} ref={ref}>
				{/* HEADER */}
				{loading ? (
					<p className="text-left text-print-xsmall text-gray-500">Memuat data...</p>
				) : (
					<div>
						<p className="text-center text-print-medium font-bold">{property?.header1 || ''}</p>
						<p className="text-center text-print-small font-semibold">{property?.header2 || ''}</p>
						<p className="text-center text-print-xsmall mt-2">{property?.header3 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.header4 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.header5 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.header6 || ''}</p>
						<div className="my-3 w-full border border-separate border-gray-400" />
						{isReprintBill && <p className="text-center text-print-small mt-3 font-semibold">THIS IS COPY / REPRINT</p>}
						{isPrintTest && <p className="text-center text-print-small mt-3 font-semibold">THIS IS PRINT TEST</p>}
					</div>
				)}

				{isPrintTest || isReprintBill ? <div className="my-3 w-full border border-separate border-gray-400" /> : null}

				<Row>
					<Col span={7}>
						<p className="text-left text-print-xsmall">Tanggal/Waktu</p>
					</Col>
					<Col span={2}>
						<p className="text-center text-print-xsmall">:</p>
					</Col>
					<Col span={15}>
						<p className="text-left text-print-xsmall">{moment(billDate).format('ddd, DD MMMM YYYY LTS')}</p>
					</Col>
				</Row>
				<Row className="my-1">
					<Col span={7}>
						<p className="text-left text-print-xsmall">No. Transaksi</p>
					</Col>
					<Col span={2}>
						<p className="text-center text-print-xsmall">:</p>
					</Col>
					<Col span={15}>
						<p className="text-left text-print-xsmall">{billNo || billNoTrx || '-'}</p>
					</Col>
				</Row>
				<Row>
					<Col span={7}>
						<p className="text-left text-print-xsmall">Kasir</p>
					</Col>
					<Col span={2}>
						<p className="text-center text-print-xsmall">:</p>
					</Col>
					<Col span={15}>
						<p className="text-left text-print-xsmall">{property?.cashier || ''}</p>
					</Col>
				</Row>

				<div className="my-3 w-full border border-separate border-gray-400" />

				{/* BODY / TRANSACTION */}
				{transactions?.length === 0 && <p className="text-left text-print-xsmall text-gray-500">Tidak ada transaksi</p>}
				{transactions?.map(({ ItemName = '', qty = 0, price = 0, total = 0 }, i) => {
					return (
						<Row wrap gutter={[2, 2]} key={i} className="mb-2">
							<Col span={24}>
								<p className="text-left text-print-xsmall">{ItemName || ''}</p>
							</Col>
							<Col span={8}>
								<p className="text-left text-print-xsmall">Rp {currencyFormat(price || 0, 0) || ''}</p>
							</Col>
							<Col span={8}>
								<p className="text-center text-print-xsmall">{qty || ''}x</p>
							</Col>
							<Col span={8}>
								<p className="text-right text-print-xsmall">Rp {currencyFormat((total || 0).toString(), 0) || '0'}</p>
							</Col>
						</Row>
					);
				})}

				<div className="my-3 w-full border border-separate border-gray-400" />

				{/* PAYMENT */}
				{payments?.length === 0 ? (
					<Row className="my-1">
						<Col span={12}>
							<p className="text-right text-print-xsmall">Pembayaran</p>
						</Col>
						<Col span={12}>
							<p className="text-right text-print-xsmall uppercase">{(paymentMethod || '-').trim().toUpperCase()}</p>
						</Col>
					</Row>
				) : (
					payments?.map(({ cashPay, tenderName, paidType, paidValue }, index) => {
						return (
							<Row className="my-1">
								<Col span={12}>
									<p className="text-right text-print-xsmall capitalize">{(tenderName || '')?.trim()}</p>
								</Col>
								<Col span={12}>
									<p className="text-right text-print-xsmall uppercase">
										Rp {(currencyFormat(+paidType === 1 ? cashPay : paidValue, 0) || '')?.trim()}
									</p>
								</Col>
							</Row>
						);
					})
				)}
				<Row className="my-1">
					<Col span={12}>
						<p className="text-right text-print-xsmall">Subtotal</p>
					</Col>
					<Col span={12}>
						<p className="text-right text-print-xsmall">Rp {currencyFormat(total || '0', 0)}</p>
					</Col>
				</Row>
				<Row className="my-1">
					<Col span={12}>
						<p className="text-right text-print-xsmall">Diskon</p>
					</Col>
					<Col span={12}>
						<p className="text-right text-print-xsmall">Rp 0</p>
					</Col>
				</Row>
				<Row className="my-1">
					<Col span={12}>
						<p className="text-right text-print-xsmall">Total</p>
					</Col>
					<Col span={12}>
						<p className="text-right text-print-xsmall">Rp {currencyFormat(total || '0', 0)}</p>
					</Col>
				</Row>
				<Row className="my-1">
					<Col span={12}>
						<p className="text-right text-print-xsmall">Bayar</p>
					</Col>
					<Col span={12}>
						<p className="text-right text-print-xsmall">Rp {currencyFormat(cashPaid || '0', 0)}</p>
					</Col>
				</Row>
				<Row className="my-1">
					<Col span={12}>
						<p className="text-right text-print-xsmall">Kembalian</p>
					</Col>
					<Col span={12}>
						<p className="text-right text-print-xsmall">Rp {currencyFormat(cashChange || '0', 0)}</p>
					</Col>
				</Row>

				{/* FOOTER */}
				{loading ? (
					<p className="text-left text-print-xsmall text-gray-500">Memuat data...</p>
				) : (
					<div className="mt-7">
						<p className="text-center text-print-xsmall font-semibold">{property?.footer1 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.footer2 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.footer3 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.footer4 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.footer5 || ''}</p>
						<p className="text-center text-print-xsmall">{property?.footer6 || ''}</p>
					</div>
				)}
			</div>
		</div>
	);
});
