import { Button, Modal } from 'antd';
import React, { useEffect, useRef } from 'react';
import { currencyFormat } from '../../../utils/helper/utilities';
import usePayment from '../hooks/usePayment';

export default function ModalCashChange(props) {
	const modalRef = useRef();

	const { handlePaymentStore = (e) => {}, state = {}, handleCancel = () => {}, open = false } = props;
	const { getCashChange } = usePayment();

	const handleFocus = () => {
		const modal = modalRef && modalRef.current !== null && modalRef.current;

		if (modal) {
			modalRef.current.focus();
		}
	};

	const handleEnterPress = (e) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			handlePaymentStore();
		}
	};

	useEffect(() => {
		const modal = modalRef && modalRef.current !== null && modalRef.current;

		if (modal) {
			handleFocus();
			modal.addEventListener('keydown', handleEnterPress);

			return () => {
				modal.removeEventListener('keydown', handleEnterPress);
			};
		}
	}, [modalRef]);

	useEffect(() => {
		handleFocus();
	}, [open]);

	return (
		<Modal centered width={475} footer={false} onCancel={handleCancel} open={open} bodyStyle={{ backgroundColor: '#f8f8f8' }}>
			<div
				autoFocus={true}
				tabIndex={1}
				ref={modalRef}
				onKeyDown={handleEnterPress}
				className="w-full h-60 flex items-center justify-center flex-col gap-3">
				<h1 className="text-lg font-medium text-gray-700">KEMBALIAN PELANGGAN</h1>
				<p className="text-3xl font-bold">Rp {currencyFormat(getCashChange(state?.settles, state?.cashPaid), 0)}</p>

				<Button className="w-1/2 btn-page h-14 mt-7" onClick={handlePaymentStore}>
					PRINT BILL
				</Button>

				<p className="text-center text-base">
					Untuk melakukan cetak struk, anda dapat <b>KLIK</b> tombol diatas, ataupun tekan <b>ENTER</b>
				</p>
			</div>
		</Modal>
	);
}
